@import './../../styles';

.timeline {
    &__heading {
        left: 10px;
        font-family: Roboto Mono, monospace;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 5px;
        text-transform: uppercase;
        font-weight: 700;
        color: #999;
        margin-bottom: 40px;
        margin-top: 40px;
    }

    &__wrapper {
        border-left: 2px solid #6d56c1;
        padding-left: 25px;
        padding-bottom: 35px;
        display: flex;
        flex-direction: column;

        &::before {
            width: 12px;
            height: 12px;
            border: 2px solid #6d56c1;
            position: relative;
            content: "";
            border-radius: 50%;
            background-color: #fff;
            left: -32px;
        }
    }

    &__title {
        text-transform: uppercase;
        color: #6d56c1;
        font-size: 14px;
        line-height: 20px;
        font-family: Roboto Mono, monospace;
        font-weight: 700;
        padding-bottom: 5px;
    }

    &__date-range {
        font-family: Inconsolata, monospace;
        font-size: 14px;
        line-height: 20px;
        color: #999;
        padding-bottom: 5px;
    }

    &__descryption {
        font-family: Inconsolata, monospace;
        font-size: 15px;
        line-height: 20px;
        color: #999;
        padding-bottom: 10px;
    }
}