@import './../../styles';
.home {
 &__description {
    color: #999;
    margin-bottom: 40px;
    margin-top: 40px;
    font-size: 36px;
    line-height: 42px;
    font-family: Roboto Mono,monospace;

    &--text {
        font-size: 18px;
        color: #999999;
        line-height: 30px;
    }
 }
 &__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
 }
 &__heading {
    left: 10px;
    font-family: Roboto Mono, monospace;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 5px;
    text-transform: uppercase;
    font-weight: 700;
    color: #999;
    margin-bottom: 40px;
    margin-top: 40px;
 }
 &__sub--heading {
    color: #000000b3;
    margin-bottom: 35px;
    font-family: Roboto Mono, monospace;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 5px;
    text-transform: uppercase;
    font-weight: 700;
    margin-top: 20px;
 }
 &__projects {
   margin-bottom: 70px;
   &--list {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      @include desktop {
         flex-direction: row;
         gap: 40px;
      }
   }
 }
}