.info-form {
    &__wrapper {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        
    }
    &__item {
        margin-top: 20px;
    }
    &__textarea {
        background-color: transparent;
        border-radius: 3px;
        border: 1px solid #fff;
        padding: 10px;
        height: 115px;
        color: #fff;
        font-size: 16px;
        line-height: 24px;
        font-family: Roboto Mono,monospace;
        margin-bottom: 15px;
        outline: none;
        &::placeholder {
            color: white;
            opacity: 1;
        }
    }
}