@import './../../styles';
.footer {
    padding-bottom: 20px;
    padding-top: 50px;
    overflow: hidden;
    z-index: 10;
    background: linear-gradient(54deg, rgba(74, 76, 179, .95) 34%, hsla(0, 34%, 71%, .95));

    &__heading {
        font-size: 36px;
        color: white;
        padding-bottom: 40px;
    }
    &__wrapper {
        display: flex;
        flex-direction: column;
        // gap: 10px;
        justify-content: space-between;
        @include desktop {
            flex-direction: row;
        }
    }
    &__info {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    &__contact--list {
        font-size: 15px;
        line-height: 30px;
        font-weight: 500;
    }
    &__item {
        font-size: 16px;
        color: white;
        padding-bottom: 10px;
    }
    &__title {
        padding-right: 10px;
        color: #ffffffb3;
        font-weight: 700;
    }
    &__link {
        text-decoration: underline;
        cursor: pointer;
        color: white;
    }

    &__latter {
        display: flex;
        flex-direction: column;
        flex-grow: 0.5;
    }
    &__form {
        display: flex;
        flex-direction: column;
        flex-grow: 0.5;
        
        input {
            background-color: transparent;
            border-radius: 3px;
            border: 1px solid #fff;
            padding: 10px;
            color: #fff;
            font-size: 16px;
            line-height: 24px;
            font-family: Roboto Mono,monospace;
            margin-bottom: 15px;
            outline: none;
            &::placeholder {
                color: white;
                opacity: 1;
            }
        }
        textarea {
            background-color: transparent;
            border-radius: 3px;
            border: 1px solid #fff;
            padding: 10px;
            height: 115px;
            color: #fff;
            font-size: 16px;
            line-height: 24px;
            font-family: Roboto Mono,monospace;
            margin-bottom: 15px;
            outline: none;
            &::placeholder {
                color: white;
                opacity: 1;
            }
        }
        &::placeholder {
            color: white;
            opacity: 1;
        }
    }
    &__copyright {
        text-align: center;
        color: white;
        text-align: center;
        margin-top: 15px;
        color: white;
        font-size: 20px;
    }
    button[disabled]{
        cursor: not-allowed;
  pointer-events: all !important;
  color: rgba($color: white, $alpha: 0.4) ;
      }
      img {
        height: 25px;
      }
      .loader {
        border: 16px solid #f3f3f3;
        border-radius: 50%;
        border-top: 16px solid #3498db;
        width: 20px;
        height: 20px;
        -webkit-animation: spin 2s linear infinite; /* Safari */
        animation: spin 2s linear infinite;
        position: absolute;
        top: 8px;
        left: 50%;
      }
      
      /* Safari */
      @-webkit-keyframes spin {
        0% { -webkit-transform: rotate(0deg); }
        100% { -webkit-transform: rotate(360deg); }
      }
      
      @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
      }
      &__success {
        color: white;
    font-size: 18px;
    text-align: center;
    margin-bottom: 10px;
    font-weight: 600;
      }
}