@import './../../styles';
.progress-wrapper {
    width: 100%;
   @include desktop {
    width: 40%;
   }
    padding-bottom: 15px;
    &__strip {
            width: 100%;
            background-color: #dee2e6;
            height: 8px;
            border-radius: 10px;
            position: relative;
            &--field {
                background: linear-gradient(54deg, rgba(74, 76, 179, .95) 24%, hsla(0, 34%, 71%, .95));
                // width: 60%;
                height: 100%;
                position: absolute;
                border-radius: 10px;
                // animation: progressBar 3s ease-in-out;
                // animation-fill-mode:both; 
                transition: "3s ease-in-out"
            }
    }
    @keyframes progressBar {
        0% { width: 0; }
        100% {width: 60%; }
      }
    &__content {
        display: flex;
        justify-content: space-between;
        color: #999;
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 10px;
    }
}