@import './../../styles';
.info {
    background: var(--app-info-background-color);
    opacity: 0.9;
    padding-top: 20px;
    padding-bottom: 20px;
   
&__wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    @include desktop {
        flex-direction: row;
    }
}
    &__user-image {
        img {
            width: 150px;
            height: 150px;
            border-radius: 50%;
        }
    }
    &__name--section {
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: flex-start;
    }
    &__name {
        color: var(--app-info-primery-color);
        font-size: 20px;
        white-space: nowrap;
        overflow: hidden;
        animation: type 4s steps(60, end);
        margin: 0px;
        font-size: 40px;
        font-family: 'Sacramento', cursive;
        line-height: 35px;
    }
    &__discryption {
        font-size: 16px;
        color: var(--app-info-primery-color);
        padding-bottom: 10px;
        text-align: left;
        &--heading {
            padding-right: 10px;
            color: var(--app-info-secondry-color);
            font-weight: 700;
        }
    }
    &__icon {
        padding-right: 20px;
        cursor: pointer;
        color: var(--app-info-primery-color);
    }
    &__heading {
        color: white;
        font-size: 30px;
        font-weight: 400;
    }

}

.info-next {
    &__description {
        color: #999;
        margin-bottom: 40px;
        margin-top: 40px;
        font-size: 36px;
        line-height: 42px;
        font-family: Roboto Mono,monospace;
    
        &--text {
            font-size: 18px;
            color: #999999;
            line-height: 30px;
        }
     }
}