.technical-skill {
    &__type {
        display: flex;
        gap: 10px;
    }
    &__dropdown {
        height: 25px;
        border-color: #dee2e6;
    }

    &__btn {
        background-color: #6d56c1;
        height: 25px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        color: white;
        padding: 0px 5px;

    }
}