@import './../../styles';
.header {
    &__navbar {
        overflow: hidden;
        z-index: 10;
        background: var(--app-header-background-color);
      }
    &__navbar--item {
        float: left;
        display: block;
        color: var(--app-header-text-color);
        text-align: center;
        padding: 24px 16px;
        text-decoration: none;
        font-size: 17px;
        cursor: pointer;
      }
    
      &__sticky {
        position: fixed;
        top: 0;
        width: 100%;
        background: var(--app-header-sticky-background-color);
        color: var(--app-header-sticky-text-color);
        transition: 0.15s ease-out;
        box-shadow: $app-header-shadow;
      }
    
      &__sticky a {
        float: left;
        display: block;
        color: var(--app-header-sticky-text-color);
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 17px;
    
      }
    
      &__sticky+.content {
        padding-top: 60px;
      }
      &__your--portfolio {
        padding-top: 10px;
        &-link {
          background: white;
          color: black;
          border-radius: 15px;
          height: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        
      }
}