.timeline-list {

    &__heading {
        margin-right: 15px;
        font-size: 15px;
        font-weight: 800;
        color: black;
    }

    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 50px;
    }

    &__label {
        margin-left: 15px;
        top: -1px;
        font-size: 15px;
        font-weight: 800;
        color: black;
    }

    &__check {
        position: relative !important;
        width: 200px;
        cursor: pointer;
    }

}