@import './../../styles';

.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 15px;
    flex: 0 0 30%;
    box-sizing: border-box;
    margin-top: 25px;
    background: linear-gradient(to bottom, #ccffcc 31%, #00ccff 90%);

    &__description {
        padding: 30px 25px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__title {
        text-align: left;
        font-size: 25px;
        color: #000000cc;
        text-transform: capitalize;
        font-weight: 700;
        padding-bottom: 10px;
    }

    &__info {
        font-size: 15px;
        text-align: left;
        font-size: 15px;
        color: #999;
        line-height: 20px;
        padding-bottom: 10px;
    }

    &__heading {
        font-size: 20px;
        padding-bottom: 10px;
        color: #5c6767;
    }

    &__item {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        transition: 0.3s;
        padding: 10px;
        margin-top: 10px;
        color: #171c1cfa;
        font-weight: 600;
        flex: auto;
        margin: 5px;
        &--list {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
        }

    }
}